/**
 * Newsletter Form Handler
 */
class NewsletterForm {
  constructor(formSelector) {
    this.form = document.querySelector(formSelector);
    this.messageEl = document.getElementById('form-message');
    
    if (this.form) {
      this.init();
    }
  }
  
  init() {
    this.form.addEventListener('submit', (e) => this.handleSubmit(e));
  }
  
  handleSubmit(e) {
    e.preventDefault();
    const email = this.form.querySelector('input[type="email"]').value;
    
    if (this.validateEmail(email)) {
      this.showMessage('üöÄ Welcome aboard! You\'re now subscribed to cosmic updates.', 'success');
      this.form.reset();
    } else {
      this.showMessage('‚ö†Ô∏è Please enter a valid email address.', 'error');
    }
  }
  
  validateEmail(email) {
    return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
  }
  
  showMessage(text, type) {
    if (this.messageEl) {
      this.messageEl.textContent = text;
      this.messageEl.className = `form-message ${type}`;
      setTimeout(() => {
        this.messageEl.textContent = '';
        this.messageEl.className = 'form-message';
      }, 5000);
    }
  }
}

/**
 * Smooth Scroll Handler
 */
class SmoothScroll {
  constructor(linkSelector) {
    this.links = document.querySelectorAll(linkSelector);
    this.init();
  }
  
  init() {
    this.links.forEach(link => {
      link.addEventListener('click', (e) => {
        const href = link.getAttribute('href');
        if (href.startsWith('#')) {
          e.preventDefault();
          const target = document.querySelector(href);
          if (target) {
            target.scrollIntoView({ behavior: 'smooth' });
          }
        }
      });
    });
  }
}

/**
 * Scroll Animations
 */
class ScrollAnimations {
  constructor(selector) {
    this.elements = document.querySelectorAll(selector);
    this.init();
  }
  
  init() {
    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('animated');
        }
      });
    }, { threshold: 0.1 });
    
    this.elements.forEach(el => observer.observe(el));
  }
}

/**
 * Easter Eggs
 */
class EasterEggs {
  constructor() {
    this.konamiCode = ['ArrowUp', 'ArrowUp', 'ArrowDown', 'ArrowDown', 'ArrowLeft', 'ArrowRight', 'ArrowLeft', 'ArrowRight', 'b', 'a'];
    this.inputSequence = [];
    this.init();
  }
  
  init() {
    document.addEventListener('keydown', (e) => {
      this.inputSequence.push(e.key);
      this.inputSequence = this.inputSequence.slice(-10);
      
      if (this.inputSequence.join(',') === this.konamiCode.join(',')) {
        this.activateRainbowMode();
      }
    });
  }
  
  activateRainbowMode() {
    document.body.classList.add('rainbow-mode');
    console.log('üåà Rainbow mode activated!');
    
    setTimeout(() => {
      document.body.classList.remove('rainbow-mode');
    }, 10000);
  }
}

/**
 * Main application class
 */
class CosmicVoyage {
  constructor() {
    this.modules = {};
    this.init();
  }

  /**
   * Initialize all application modules
   */
  init() {
    // Wait for DOM to be ready
    if (document.readyState === 'loading') {
      document.addEventListener('DOMContentLoaded', () => this.bootstrap());
    } else {
      this.bootstrap();
    }
  }

  /**
   * Bootstrap all modules
   */
  bootstrap() {
    try {
      // Core visual effects
      this.modules.starfield = new Starfield('#starfield');

      // Navigation
      this.modules.mobileMenu = new MobileMenu('.hamburger', '.nav-menu');

      // Interactive features
      this.modules.planetInteraction = new PlanetInteraction('.planet-body', '#planet-info');
      this.modules.newsletterForm = new NewsletterForm('#subscribe-form');

      // Utilities
      this.modules.smoothScroll = new SmoothScroll('a[href^="#"]');
      
      // Animate elements on scroll
      const animatedElements = document.querySelectorAll('.fact-card, .section-header');
      animatedElements.forEach((el, i) => {
        el.classList.add('animate-on-scroll');
        el.style.animationDelay = `${i * 100}ms`;
      });
      this.modules.scrollAnimations = new ScrollAnimations('.animate-on-scroll');

      // Fun stuff
      this.modules.easterEggs = new EasterEggs();

      console.log('üöÄ Cosmic Voyage initialized! Welcome to space exploration.');
    } catch (error) {
      console.error('‚ùå Failed to initialize Cosmic Voyage:', error);
    }
  }
}

// Initialize the app
new CosmicVoyage();
